import  {QPoint, QCircle, QRectangle, QuadTree} from '../../build/dist';


QuadTree.sub('create', function(qt){
    console.log('create', qtree);
});

var  qtree = new QuadTree(new QRectangle(
    1500 / 2, 1500 / 2, //x,y center
    1500,1500  //w,h
), 4);

for (let i = 0; i < 5000; i++) {
    let p = new QPoint({
        x: Math.random() * 500,
        y: Math.random() * 500},
        {}
    );
    qtree.insert(p);
}



